<template>
  <div class="ranking">
    <van-nav-bar title="排行榜" left-arrow class="van-head" @click-left="back()">
    </van-nav-bar>

    <div class="ranking-item">
      <div class="heading">
        <van-tabs
          v-model="active"
          color="#b83320"
          title-inactive-color="#8a8a8f"
          title-active-color="#b83320"
          @click="topType"
        >
          <van-tab :title="item.title" v-for="(item,index) in TypeData" :key="index">
            <div class="clearfix ranking-box">
              <div class="subheading fl">
                <div class="subheading-y">
                  <div
                    class="subheading-title"
                    v-for="(type,indexs) in listTypeData[item.type]"
                    :key="indexs"
                    @click="leftType(type,indexs)"
                    :class="{active:indexs == currentIndex ? 'active' : ''}"
                  >
                    <div class="subheading-bb">{{type.shortTitle}}</div>
                  </div>
                </div>
              </div>
              <div class="fr ranking-list">
                <div class="ranking-list-y">
                  <div class="clearfix" v-for="(item,index) in booksData" :key="index" @click="getBookDetial(item._id)">
                    <div class="fl ranking-list-img">
                      <img class="auto-img" :src="item.cover" alt />
                    </div>
                    <div class="fr ranking-list-box">
                      <div class="ranking-list-title">{{item.title}}</div>
                      <div class="ranking-list-content">{{item.shortIntro}}</div>
                      <div class="clearfix ranking-label">
                        <div class="fl ranking-author">{{item.author}}</div>
                        <div class="fr ranking-type">{{item.majorCate}}</div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </van-tab>
        </van-tabs>
      </div>
    </div>
  </div>
</template>
    
<script>
import "../assets/css/ranking.less";
export default {
  data() {
    return {
      TypeData: [
        {
          title: "男频",
          type: "male",
          isActive: true,
        },
        {
          title: "女频",
          type: "female",
          isActive: false,
        },
        {
          title: "出版",
          type: "epub",
          isActive: false,
        },
      ],
      //所有数据
      listTypeData: {},
      //头部导航索引值
      topIndex: 0,
      // 侧边导航索引值
      leftIndex: 0,
      // 左侧数据
      // leftData:[],
      //右侧数据
      booksData: [],
      active: 0,
      //侧边栏当前索引
      currentIndex: 0,
      //侧边栏数据详情id
      rankId: "",
      // 书籍详情id
      booksId: ''
    };
  },
  created() {
    this.getBookList();
  },
  methods: {
    //返回
    back() {
      this.$router.go(-1);
    },
    //跳转路由
    toggleTabBar(name) {
      this.$router.push(name);
    },
    // 获取数据
    getBookList() {
      this.axios({
        method: "GET",
        url: "http://novel.kele8.cn/rank-category",
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data) {
            this.listTypeData = result.data;
            // 获取头部导航栏索引值
            let type = this.TypeData[this.topIndex].type;
            this.rankId = this.listTypeData[type][this.leftIndex]._id;

            //根据id获取右边数据
            this.axios({
              methods: "GET",
              url: "http://novel.kele8.cn/rank/" + this.rankId,
            })
              .then((result) => {
                if (result.status == 200) {
                  this.booksData = result.data.ranking.books;
                }
                //遍历booksData数组，将图片解码成可读格式
                this.booksData.forEach((v) => {
                  v.cover = unescape(v.cover).slice(7);
                })
              })
              .catch((err) => {
              });
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //上边的点击事件
    topType(index) {
      this.topIndex = index;

      //切换头部导航时，初始化默认为激活左侧第一个类型
      this.leftIndex = 0;
      this.currentIndex = 0;
      this.getBookList();
    },
    // 侧边点击事件
    leftType(item, index) {
      this.leftIndex = index;
      this.currentIndex = index;
      this.getBookList();
    },

    getBookDetial(id){
      this.$router.push({name:'Detail',query:{id:id}})
    }
  },
};
</script>